.page-header {
  background-color: #fdfdfd;
  border-bottom: 1px solid #ebebeb;
  padding: 50px 0;
}

@media (max-width: 800px) {
  .page-header {
    padding: 25px 0;
  }
}

.page-header h2 {
  font-size: 33px;
  font-weight: 300;
}

@media (max-width: 800px) {
  .page-header h2 {
    font-size: 25px;
  }
}

.page-header h2,
.page-header p {
  margin: 0;
}

.page-header .page-header-icon {
  width: 64px;
  vertical-align: middle;
  display: inline-block;
  margin-right: 20px;
}

@media (max-width: 800px) {
  .page-header .page-header-icon {
    margin-right: 20px;
  }
}

.page-header .page-header-icon img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media (max-width: 800px) {
  .page-header .page-header-icon {
    width: 48px;
  }
}

.page-header .page-header-title {
  display: inline-block;
  vertical-align: middle;
}

.page-navigation {
  float: left;
  width: 230px;
  padding: 60px 0;
  border-right: 1px solid #ebebeb;
}

@media (max-width: 800px) {
  .page-navigation {
    float: none;
    padding: 25px 0;
    border: 0 none;
    width: auto;
  }

  .page-navigation nav h5,
  .page-navigation nav a {
    font-size: 15px;
  }
}

.page-navigation h5 {
  font-family: 'Whitney SSm SmallCaps A', 'Whitney SSm SmallCaps B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 13px;
  color: #49504e;
  font-weight: 500;
  margin-bottom: 8px;
  line-height: 1.6;
  text-transform: lowercase;
}

.page-navigation nav > ul {
  margin-bottom: 1.5em;
}

.page-navigation li {
  line-height: 30px;
}

.page-navigation li ul a {
  padding-left: 30px;
  color: #9da9b3;
}

.page-navigation a {
  padding: 0 17px;
  border-left: 1px solid #ebebeb;
}

.page-content-container {
  padding-left: 230px;
}

@media (max-width: 800px) {
  .page-content-container {
    padding-left: 0;
  }
}

.page-content {
  padding: 0 50px;
}

.content {
  padding: 60px 0;
  font-size: 16px;
  font-family: 'Whitney A', 'Whitney B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.5;
}

@media (max-width: 800px) {
  .content {
    padding: 0;
  }
}

.content h1,
.content h2,
.content h3 {
  margin-bottom: 1em;
  font-weight: 600;
}

.content h1 {
  font-size: 34px;
}

.content h2 {
  font-size: 24px;
  border-bottom: 1px solid #ebebeb;
  padding-bottom: .5em;
  margin-bottom: .5em;
}

.content h3 {
  font-size: 23px;
  color: #40805b;
  font-weight: 500;
  margin-bottom: 0.5em;
}

.content p {
  margin-bottom: 1em;
}

.content a {
  text-decoration: none;
  color: #23ab5f;
}

.content a:hover {
  text-decoration: underline;
}

.content hr {
  border-top: 4px solid #ebebeb;
}

.content img {
  display: block;
  max-width: 100%;
  height: auto;
}

.content pre {
  background-color: #f2f3f4;
  border: 1px solid #eceeef;
  color: #5a6166;
  line-height: 24px;
  margin: 1em 0;
  padding: 8px 13px;
  border-radius: 4px;
}

.content pre code {
  padding: 0;
  color: #505659;
  background: transparent;
}

.content pre:hover {
  overflow: auto;
}

.page-download .content ol {
  list-style: none;
  padding-left: 26px;
  counter-reset: step;
}

.page-download .content ol li {
  position: relative;
  padding: 10px 0 20px 44px;
  border-left: 1px solid #ebebeb;
  margin: 0;
}

.page-download .content ol li *:last-child {
  margin-bottom: 0;
}

.page-download .content ol li:last-child {
  border: 0 none;
}

.page-download .content ol li:before {
  counter-increment: step;
  content: counter(step);
  box-sizing: border-box;
  width: 50px;
  height: 50px;
  line-height: 48px;
  text-align: center;
  border-radius: 50%;
  border: 1px solid #ebebeb;
  background-color: #fff;
  font-weight: 600;
  color: #40805b;
  position: absolute;
  left: -26px;
  top: 0;
}

.section {
  padding: 60px 0;
}

@media (max-width: 550px) {
  .section {
    padding: 20px 0;
  }
}

.section-header {
  text-align: center;
  margin-bottom: 45px;
}

.section-header h2 {
  margin: 0;
  color: #45494d;
}

.section-header p {
  color: #74777a;
  margin: 5px 0 0;
}

.section-buttons {
  margin-top: 20px;
  text-align: center;
}

.features-list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}

.features-list h3 {
  font-family: 'Whitney A', 'Whitney B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 21px;
  font-weight: 600;
  margin-bottom: 30px;
  color: #45494d;
}

.features-list img {
  margin-bottom: 20px;
}

.features-list p {
  margin: 0;
  line-height: 22px;
  font-weight: 500;
  color: #667580;
}

@media (max-width: 550px) {
  .features-list p {
    margin-bottom: 2.5rem;
  }
}

.features-list p + p {
  margin-top: 15px;
}

.intro-section {
  display: table;
  width: 100%;
  text-align: center;
  padding: 4rem 0;
}

.intro-section .intro-section-well {
  display: table-cell;
  vertical-align: middle;
}

.intro-section h2 {
  font-family: 'Whitney A', 'Whitney B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 28px;
  font-weight: 600;
  color: #323638;
}

.intro-section p {
  font-size: 17px;
  line-height: 27px;
  font-weight: 500;
}

.intro-section .button {
  margin: 0 10px 10px 0;
}

@media (min-width: 1000px) {
  .intro-section {
    height: 450px;
    text-align: left;
    background-size: contain;
    background: url("../assets/images/net-1x.png") no-repeat right center;
    padding: 0;
  }
}

.social-buttons-section {
  text-align: center;
  padding: 20px 0;
  background-color: #fbfbfc;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
}

.social-buttons-section ul {
  list-style: none;
  margin: 0;
  line-height: 0;
}

.how-it-works-section {
  background-color: #fbfbfc;
  border-top: 1px solid #f0f0f0;
  border-bottom: 1px solid #f0f0f0;
  text-align: center;
  padding-bottom: 0;
}

.how-it-works-section .section-header {
  margin-bottom: 50px;
}

.how-it-works-section .features-list {
  margin-top: 45px;
}

.how-it-works-section h4 {
  font-family: 'Whitney SSm SmallCaps A', 'Whitney SSm SmallCaps B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 13px;
  color: #929da6;
  position: relative;
  font-weight: 500;
  z-index: 1;
  margin: 0;
}

.how-it-works-section h4 span {
  background-color: #fbfbfc;
  padding: 0 15px;
}

.how-it-works-section h4:before {
  content: "";
  position: absolute;
  background-color: #f0f0f0;
  height: 1px;
  width: 100%;
  left: 0;
  top: 50%;
  z-index: -1;
}

.how-it-works-section .section-buttons {
  margin-bottom: 85px;
}

.powered-list .list-inline {
  display: table;
  width: 100%;
}

.powered-list .list-inline .powered-list-item {
  display: table-cell;
  height: 85px;
  vertical-align: middle;
}

.looking-for-help-section .features-list h3 {
  margin-bottom: 15px;
}

.looking-for-help-section .features-list img {
  margin-bottom: 10px;
}

.looking-for-help-section .features-list.learn-more.row li {
  position: relative;
  margin-bottom: 0;
}

.looking-for-help-section .features-list.learn-more.row li + .columns:after {
  content: "";
  position: absolute;
  top: 0;
  left: -8%;
  width: 2px;
  height: 100%;
  background-color: #f2f2f2;
}

@media (min-width: 550px) and (max-width: 750px) {
  .features-list:not(.learn-more) img {
    width: 90%;
  }

  .powered-list .list-inline .powered-list-item:not(.vip) {
    display: none;
  }

  .learn-more h3 {
    font-size: 15px;
  }
}

@media (max-width: 550px) {
  .powered-list {
    display: none;
  }

  .learn-more li:not(:first-of-type) h3 {
    margin-top: 25px;
  }
}

.page-contribute section {
  padding: 40px 0;
}

.page-contribute section + section {
  border-top: 2px solid #e1e4e5;
}

.page-contribute .content {
  padding: 0;
}

.page-contribute .content h2 {
  color: #262d33;
  font-size: 32px;
  border: 0 none;
  padding: 0;
  margin-bottom: 0.8em;
}

.page-contribute .content h3 {
  font-size: 28px;
  color: #505659;
}

.page-contribute .content h4 {
  font-size: 19px;
  font-weight: 600;
  margin: 1.7em 0 0.5em 0;
}

.page-contribute .content p {
  margin-bottom: 1.2em;
}

.page-contribute .content pre {
  margin: 1.2em 0;
}

.page-contribute .content hr {
  border-top: 1px solid #e1e4e5;
  margin: 5rem 0;
}

.page-contribute .toggle-page-section:before {
  content: "\203a";
  display: inline-block;
  vertical-align: text-top;
  line-height: 1;
  width: 20px;
}

.page-contribute .page-section {
  display: none;
}

.page-download .select-download-method {
  text-align: center;
  margin-bottom: 60px;
}

.page-download .select-download-method h2 {
  font-family: 'Whitney A', 'Whitney B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 32px;
  color: #000;
  font-weight: 600;
  margin: 0;
}

.page-download .select-download-method p {
  margin: 8px 0 0;
}

.page-download .tab-list-wrap {
  border-bottom: 1px solid #bde6cf;
  padding-bottom: 40px;
}

.page-download .tab-list {
  list-style: none;
  padding: 0;
  margin: 0 -20px 0 -20px;
}

.page-download .tab-list li {
  float: left;
  margin: 0;
  width: 20%;
  padding: 0 20px;
  box-sizing: border-box;
  position: relative;
}

@media (max-width: 900px) {
  .page-download .tab-list li {
    padding: 0 10px;
  }
}

@media (max-width: 800px) {
  .page-download .tab-list li {
    float: none;
    width: auto;
    margin-bottom: 10px;
  }
}

.page-download .tab-list li:before,
.page-download .tab-list li:after {
  display: none;
  content: "";
  position: absolute;
  bottom: -41px;
  border: 0 none;
  width: 0;
  height: 0;
  left: 50%;
  margin-left: -8px;
}

.page-download .tab-list li:before {
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 9px solid #bde6cf;
}

.page-download .tab-list li:after {
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 8px solid #fff;
  margin-left: -7px;
}

.page-download .tab-list a {
  display: block;
  text-align: center;
  border: 1px solid #23ab5f;
  background-image: linear-gradient(to top, rgba(189, 230, 207, 0.15) 0%, rgba(255, 255, 255, 0.15) 100%);
  font-size: 15px;
  height: 48px;
  line-height: 48px;
  white-space: nowrap;
  padding: 0;
}

.page-download .tab-list .active:before,
.page-download .tab-list .active:after {
  display: block;
}

@media (max-width: 800px) {
  .page-download .tab-list .active:before,
  .page-download .tab-list .active:after {
    display: none;
  }
}

.page-download .tab-list .active a {
  text-shadow: 0 -1px 0 rgba(0, 1, 1, 0.2);
  padding: 1px;
  border: 0 none;
  cursor: default;
  background-color: #23ab5f;
  background-image: linear-gradient(to bottom, #0b803b, #149652);
  color: #fff;
  box-shadow: inset 0 1px rgba(0, 0, 0, 0.3);
}

.page-download .tab-content .tab-pane {
  display: none;
}

.page-download .tab-content .active {
  display: block;
}

.page-download .content {
  padding: 35px 0 0;
}

.page-download .content h3 {
  font-size: 28px;
  color: #505659;
  margin-bottom: .7em;
}

.page-download .content ol {
  padding-left: 19px;
}

.page-download .content ol li {
  padding: 7px 0 30px 30px;
}

.page-download .content ol li:last-child {
  padding-bottom: 15px;
}

.page-download .content ol li:before {
  color: #505659;
  font-size: 13px;
  width: 36px;
  height: 36px;
  line-height: 36px;
  left: -19px;
}

.page-download section {
  padding: 80px 0;
}

.page-download .faq-section {
  background-color: #fdfdfd;
  border-top: 2px solid #e1e4e5;
  border-bottom: 2px solid #e1e4e5;
}

.page-download .faq-section h4 {
  font-size: 19px;
  font-family: 'Whitney A', 'Whitney B', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 600;
  margin-bottom: 0.5em;
}

.page-download .faq-section p {
  margin-bottom: 0.5em;
}

.page-download .faq-section ul {
  margin: 0;
}

.page-download .faq-section ul li {
  margin: 0;
  list-style: none;
}

.page-download .faq-section ul li:before {
  content: "\203a";
  display: inline-block;
  width: 10px;
  color: #23ab5f;
}

.page-download .subscribe-section {
  min-height: 150px;
}

.page-download .subscribe-section .form-message {
  display: none;
  font-size: 20px;
  text-align: center;
  line-height: 44px;
}

.page-download .subscribe-section .success-message {
  color: #23ab5f;
}

.page-download .subscribe-section .error-message {
  color: #bb2616;
}

.page-download .subscribe-section h2 {
  text-align: center;
  font-size: 28px;
  margin-bottom: 30px;
}

.page-download .subscribe-section input,
.page-download .subscribe-section button {
  width: 100%;
  box-sizing: border-box;
}
